import type { IotaClient } from '@iota/iota-sdk/client';
import { Ed25519Keypair } from '@iota/iota-sdk/keypairs/ed25519';
import { Transaction } from '@iota/iota-sdk/transactions';
import { toB64 } from '@iota/iota-sdk/utils';
import type {
    StandardConnectFeature,
    StandardConnectMethod,
    StandardEventsFeature,
    StandardEventsOnMethod,
    IotaFeatures,
    IotaSignAndExecuteTransactionMethod,
    IotaSignPersonalMessageMethod,
    IotaSignTransactionMethod,
    Wallet,
} from '@iota/wallet-standard';
import { getWallets, ReadonlyWalletAccount, SUPPORTED_CHAINS } from '@iota/wallet-standard';
import { useEffect } from 'react';

import { useIotaClient } from '../useIotaClient.js';

const WALLET_NAME = 'Unsafe Burner Wallet';

export function useUnsafeBurnerWallet(enabled: boolean) {
    const iotaClient = useIotaClient();

    useEffect(() => {
        if (!enabled) {
            return;
        }
        const unregister = registerUnsafeBurnerWallet(iotaClient);
        return unregister;
    }, [enabled, iotaClient]);
}

function registerUnsafeBurnerWallet(iotaClient: IotaClient) {
    const walletsApi = getWallets();
    const registeredWallets = walletsApi.get();

    if (registeredWallets.find((wallet) => wallet.name === WALLET_NAME)) {
        console.warn(
            'registerUnsafeBurnerWallet: Unsafe Burner Wallet already registered, skipping duplicate registration.',
        );
        return;
    }

    console.warn(
        'Your application is currently using the unsafe burner wallet. Make sure that this wallet is disabled in production.',
    );

    const keypair = new Ed25519Keypair();
    const account = new ReadonlyWalletAccount({
        address: keypair.getPublicKey().toIotaAddress(),
        publicKey: keypair.getPublicKey().toIotaBytes(),
        chains: ['iota:unknown'],
        features: ['iota:signTransaction', 'iota:signAndExecuteTransaction'],
    });

    class UnsafeBurnerWallet implements Wallet {
        get version() {
            return '1.0.0' as const;
        }

        get name() {
            return WALLET_NAME;
        }

        get icon() {
            return 'data:image/png;base64,' as const;
        }

        // Return the IOTA chains that your wallet supports.
        get chains() {
            return SUPPORTED_CHAINS;
        }

        get accounts() {
            return [account];
        }

        get features(): StandardConnectFeature & StandardEventsFeature & IotaFeatures {
            return {
                'standard:connect': {
                    version: '1.0.0',
                    connect: this.#connect,
                },
                'standard:events': {
                    version: '1.0.0',
                    on: this.#on,
                },
                'iota:signPersonalMessage': {
                    version: '1.0.0',
                    signPersonalMessage: this.#signPersonalMessage,
                },
                'iota:signTransaction': {
                    version: '2.0.0',
                    signTransaction: this.#signTransaction,
                },
                'iota:signAndExecuteTransaction': {
                    version: '2.0.0',
                    signAndExecuteTransaction: this.#signAndExecuteTransaction,
                },
            };
        }

        #on: StandardEventsOnMethod = () => {
            return () => {};
        };

        #connect: StandardConnectMethod = async () => {
            return { accounts: this.accounts };
        };

        #signPersonalMessage: IotaSignPersonalMessageMethod = async (messageInput) => {
            const { bytes, signature } = await keypair.signPersonalMessage(messageInput.message);
            return { bytes, signature };
        };

        #signTransaction: IotaSignTransactionMethod = async (transactionInput) => {
            const { bytes, signature } = await Transaction.from(
                await transactionInput.transaction.toJSON(),
            ).sign({
                client: iotaClient,
                signer: keypair,
            });

            transactionInput.signal?.throwIfAborted();

            return {
                bytes,
                signature: signature,
            };
        };

        #signAndExecuteTransaction: IotaSignAndExecuteTransactionMethod = async (
            transactionInput,
        ) => {
            const { bytes, signature } = await Transaction.from(
                await transactionInput.transaction.toJSON(),
            ).sign({
                client: iotaClient,
                signer: keypair,
            });

            transactionInput.signal?.throwIfAborted();

            const { rawEffects, digest } = await iotaClient.executeTransactionBlock({
                signature,
                transactionBlock: bytes,
                options: {
                    showRawEffects: true,
                },
            });

            return {
                bytes,
                signature,
                digest,
                effects: toB64(new Uint8Array(rawEffects!)),
            };
        };
    }

    return walletsApi.register(new UnsafeBurnerWallet());
}
